.examples {
  background-color: var(--gray);

  &__container {
    padding: 50px 25px;

    @include media($desktop) {
      padding: 145px 157px;
    }
  }

  &__title {
    @include t-font-mobile;
    color: var(--dark);
    margin-top: 0;
    margin-bottom: 30px;

    @include media($desktop) {
      @include t-font-desktop;
      margin-bottom: 50px;
    }
  }

  &__list {
    @include reset-list;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    gap: 40px;

    @include media($tablet) {
      grid-template-columns: repeat(2, 1fr);
      gap: 50px;
    }

    @include media($desktop) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__item {
    position: relative;

    &:nth-child(3n + 1) {
      z-index: 1;

      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 55%;
        left: -40px;
        width: 130px;
        height: 130px;
        border-radius: 50%;
        border: 18px solid var(--turquoise);
        z-index: -1;

        @include media($tablet) {
          width: 150px;
          height: 150px;
          left: -50px;
        }

        @include media($desktop) {
          width: 180px;
          height: 180px;
          top: 65%;
          left: -60px;
        }
      }
    }

    &:nth-child(3n + 2) {
      @include media($desktop) {
        padding-top: 25px;

        &::before {
          content: "";
          display: block;
          position: absolute;
          top: 10px;
          left: -15px;
          width: 47px;
          height: 47px;
          border: 8px solid var(--turquoise);
        }

        &::after {
          content: "";
          display: block;
          position: absolute;
          right: -20px;
          bottom: 35px;
          width: 47px;
          height: 47px;
          border: 8px solid var(--turquoise);
        }
      }
    }

    &:nth-child(3n + 3) {
      @include media($tablet) {
        grid-column: 1 / -1;
      }

      @include media($desktop) {
        grid-column: 3 / -1;
      }

      &::before {
        content: "";
        display: block;
        position: absolute;
        top: -35px;
        right: -25px;
        width: 68px;
        height: 68px;
        border-radius: 50%;
        border: 13px solid var(--turquoise);

        @include media($desktop) {
          right: -35px;
        }
      }
    }

    &-title {
      margin: 0;
      text-align: center;
      font-weight: 600;
      font-size: 22px;
      line-height: 34px;
      color: var(--dark);
      letter-spacing: -0.5px;

      @include media($tablet) {
        max-width: 670px;
      }
    }
  }

  &__link {
    @include reset-link;
    transition: outline 0.3s ease-in-out;

    &:focus-visible {
      outline: 3px solid var(--turquoise-intaractive);
      outline-offset: 5px;
    }
  }

  &__figure {
    margin: 0;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;

    &-image {
      margin-bottom: 20px;
      object-fit: cover;
      border-radius: 8px;

      @include media($desktop) {
        width: 342px;
        height: 503px;
      }
    }
  }
}
