.nav {
  display: none;

  @include media($desktop) {
    display: block;
    position: initial;
    padding: 0 15px;
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid var(--h-border);
  }

  &--opened {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-color: var(--white);
    padding-top: 100px;
    padding-bottom: 140px;
    box-shadow: 0 2px 15px 1px var(--d-border);
  }

  &__list {
    @include reset-list;
    display: flex;
    flex-flow: column nowrap;

    @include media($desktop) {
      flex-flow: row wrap;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__item {
    position: relative;
    text-align: center;
    background-color: var(--deepwhite);

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    @include media($desktop) {
      background-color: transparent;

      &:not(:last-child) {
        margin-bottom: 0;
        margin-right: 135px;
      }
    }
  }

  &__link {
    @include reset-link;
    padding: 17px 40px 17px 16px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    text-align: left;
    transition: opacity 0.2s ease-in-out, color 0.2s ease-in-out;
    color: var(--lightgray);

    &:hover {
      color: var(--deepdark);
    }

    &:hover + &-decoration {
      border-color: var(--deepdark);
    }

    &:focus-visible {
      color: var(--deepdark);
      outline: 1px solid var(--deepdark);
    }

    &:focus-visible + &-decoration {
      border-color: var(--deepdark);
    }

    @include media($desktop) {
      padding: 21px 0 9px;
      font-weight: 300;
      font-size: 16px;
      line-height: 32px;
      text-align: center;
      opacity: 0.7;
      color: var(--white);
      
      &:hover,
      &:focus-visible {
        color: var(--white);
        opacity: 1;
        outline: none;
      }

      &:hover + &-decoration,
      &:focus-visible + &-decoration {
        border-color: none;
        opacity: 1;
      }
    }
  }

  &__link-decoration {
    position: absolute;
    top: 50%;
    right: 30px;
    display: inline-block;
    width: 12px;
    height: 12px;
    border-style: solid;
    border-color: var(--d-border);
    border-width: 0 2px 2px 0;
    transform: rotate(-45deg) translateY(-50%);
    transition: opacity 0.2s ease-in-out, border-color 0.3s ease-in-out;

    @include media($desktop) {
      width: 100%;
      height: 1px;
      top: initial;
      right: initial;
      border: none;
      left: 0;
      bottom: -1px;
      transform: none;
      background-color: var(--white);
      opacity: 0;
    }
  }
}
