.about {
  background-color: var(--white);

  &__container {
    padding-left: 17px;
    padding-right: 17px;
    padding-bottom: 70px;
    
    @include media($desktop) {
      padding-top: 150px;
      padding-left: 137px;
      padding-right: 137px;
      padding-bottom: 130px;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 30px;
    @include t-font-mobile;
    letter-spacing: -0.011em;
    color: var(--black);

    @include media($desktop) {
      margin-bottom: 50px;
      @include t-font-desktop;
    }
  }

  &__list {
    @include reset-list;
    display: grid;
    grid-template-columns: 1fr;
    gap: 35px 0;
    align-items: center;
    justify-items: center;
    counter-reset: count-items;

    @include media($tablet) {
      grid-template-columns: repeat(2, 1fr);
      gap: 40px 30px;
    }

    @include media($desktop) {
      grid-template-columns: repeat(4, 1fr);
      gap: 38px;
    }
  }

  &__description {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 34px;
    color: var(--black);
    text-align: center;

    @include media($tablet) {
      max-width: 265px;
    }

    @include media($desktop) {
      font-size: 22px;
    }

    &::before {
      counter-increment: count-items;
      content: "0" counter(count-items);
      display: block;
      color: var(--turquoise);
      font-weight: 700;
      font-size: 28px;
      line-height: 38px;
      margin-bottom: 20px;
    }
  }
}
