.contacts {
  background-color: var(--white);
  
  &__location {
    padding-top: 119px;
    padding-left: 17px;
    padding-right: 17px;
    margin-bottom: 25px;

    @include media($tablet) {
      padding-top: 215px;
    }

    @include media($desktop) {
      padding-top: 248px;
      margin-bottom: 42px;
    }
  }

  &__social {
    padding-left: 17px;
    padding-right: 17px;
    padding-bottom: 50px;

    @include media($desktop) {
      padding-bottom: 90px;
    }

    &-list {
      @include reset-list;
      display: grid;
      justify-content: center;
      margin: 0 auto;
      max-width: 1240px;

      @include media($tablet) {
        grid-template-columns: repeat(2, 1fr);
      }
    }

    &-item {
      display: flex;
      align-items: center;
      justify-content: center;

      &:not(:last-child) {
        margin-bottom: 22px;

        @include media($tablet) {
          margin-bottom: 0;
        }
      }
    }

    &-link {
      @include reset-link;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: var(--r-grey);
      order: 2;
      transition: color 0.2s ease-in-out, outline 0.3s ease-in-out;

      &:hover {
        color: var(--r-black);
      }

      &:hover + svg {
        fill: var(--turquoise-intaractive);
      }

      &:focus {
        outline: 1px solid var(--turquoise-intaractive);
        outline-offset: 3px;
      }

      &:focus + svg {
        fill: var(--turquoise-intaractive);
      }

      &-decoration {
        display: inline-block;
        order: 1;
        margin-right: 15px;
        width: 26px;
        height: 20px;
        fill: var(--turquoise);
        transition: fill 0.2s ease-in-out;
      }
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    text-align: center;

    @include media($desktop) {
      @include t-font-desktop;
      margin-bottom: 30px;
    }
  }

  &__description {
    margin: 0 auto;
    font-weight: 400;
    font-size: 17px;
    line-height: 31px;
    color: var(--r-black);
    text-align: center;
  }

  &__map {
    min-width: 320px;
    min-height: 250px;
    margin-bottom: 30px;
    background-color: var(--darkgray);

    @include media($tablet) {
      min-height: 545px;
    }

    @include media($desktop) {
      max-width: 957px;
      margin: 0 auto;
      margin-bottom: 40px;
    }
  }
}
