.form {
  max-width: 360px;
  
  &__set {
    @include reset-fieldset;
  }

  &__legend {
    margin: 0 auto;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.005em;
    color: var(--black);

    
    @include media($desktop) {
      color: var(--white);
    }
  }

  &__input {
    @include reset-input;
    min-width: 100%;
    padding: 13px 26px 11px;
    border: 1px solid var(--f-border);
    border-radius: 6px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--darkgray);
    transition: outline 0.3s ease-in-out;

    &::placeholder {
      opacity: 0.5;
    }

    &:focus-visible {
      outline: 1px solid var(--turquoise);
      outline-offset: 2px;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__button {
    @include reset-button;
    min-width: 100%;
    padding: 13px 28px;
    background-color: var(--turquoise);
    border-radius: 6px;
    color: var(--black);
    font-weight: 600;
    font-size: 15px;
    line-height: 21px;
    transition: background-color 0.2s ease-in-out, outline 0.3s ease-in-out;

    &:hover {
      background-color: var(--turquoise-intaractive);
    }

    &:focus-visible {
      background-color: var(--turquoise-intaractive);
      outline: 1px solid var(--turquoise-intaractive);
      outline-offset: 2px;
    }
  }
}
