// Flex
@mixin flex-b-c {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin flex-s-c {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@mixin flex-all-c {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-e-c {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

// Resets
@mixin reset-button {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
}

@mixin reset-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

@mixin reset-link {
  display: block;
  color: inherit;
  text-decoration: none;
}

@mixin reset-input {
  padding: 0;
  border: 0;
  font-family: inherit;
}

@mixin reset-fieldset {
  padding: 0;
  border: none;
}

// Fonts
@mixin t-font-mobile {
  font-weight: 600;
  font-size: 24px;
  line-height: 41px;
  text-align: center;
}

@mixin t-font-desktop {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
}

// Media
@mixin media($width) {
  @media (min-width: $width) {
    @content;
  }
}

@mixin burger {
  &::before {
    content: "";
    width: 23px;
    height: 1px;
    position: absolute;
    top: 18px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--black);
  }

  &::after {
    content: "";
    width: 23px;
    height: 1px;
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--black);
  }

  &-decoration {
    display: block;
    width: 23px;
    height: 1px;
    position: absolute;
    top: 24px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--black);
  }
}