.video {
  background-color: var(--deepdark);

  &__container {
    padding-top: 84px;
    padding-left: 17px;
    padding-right: 17px;
    padding-bottom: 140px;
    text-align: center;

    @include media($desktop) {
      padding: 120px 255px 480px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    color: var(--white);
    margin-top: 0;
    margin-bottom: 20px;

    @include media($desktop) {
      @include t-font-desktop;
    }
  }

  &__description {
    max-width: 340px;
    margin: 0 auto;
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.005em;
    color: var(--white);
    opacity: 0.7;
  }

  &__box {
    margin-bottom: -200px;

    @include media($tablet) {
      min-width: 503px;
      min-height: 282px;
      margin-bottom: -300px;
    }

    @include media($desktop) {
      min-width: 929px;
      min-height: 522px;
      margin-bottom: -550px;
    }
  }
}
