.hero {
  @include media($desktop) {
    position: relative;
  }

  &__about {
    background-color: var(--black);
    background-image: url(../images/hero/bike.webp);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-resolution: 2dppx) {
      background-image: url(../images/hero/bike@2x.webp);
    }

    &-container {
      min-width: 320px;
      text-align: center;
      padding: 120px 20px 70px;

      @include media($tablet) {
        padding: 135px 120px 80px;
      }

      @include media($desktop) {
        padding: 315px 280px 240px;
      }
    }
  }

  &__contacts {
    @include media($desktop) {
      position: absolute;
      bottom: 240px;
      right: 280px;
    }

    &-container {
      display: flex;
      justify-content: center;
      padding: 70px 20px;

      @include media($desktop) {
        padding: 0;
      }
    }
  }

  &__logo {
    display: none;

    @include media($desktop) {
      display: block;
      margin-bottom: 30px;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    letter-spacing: -0.005em;
    color: var(--white);

    @include media($desktop) {
      max-width: 450px;
      text-align: start;
      font-size: 46px;
      line-height: 64px;
    }
  }

  &__description {
    margin: 0;
    font-weight: 300;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    color: var(--white);
    opacity: 0.8;

    @include media($tablet) {
      max-width: 435px;
      margin: 0 auto;
    }

    @include media($desktop) {
      margin: 0;
      max-width: 450px;
      font-size: 20px;
      line-height: 32px;
      text-align: start;
    }
  }
}
