.header {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  z-index: 30;

  &__container {
    @include flex-b-c;
    padding: 25px 70px 5px 20px;

    @include media($desktop) {
      padding: 0 280px;
    }
  }

  &__logo {
    @include media($desktop) {
      display: none;
    }
  }

  &__burger {
    @include reset-button;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    background-color: var(--turquoise);

    &::before {
      content: "";
      width: 23px;
      height: 1px;
      position: absolute;
      top: 18px;
      left: 50%;
      transform: translateX(-50%);
      transition: transform 0.2s ease-in-out;
      background-color: var(--black);
    }

    &::after {
      content: "";
      width: 23px;
      height: 1px;
      position: absolute;
      top: 30px;
      left: 50%;
      transform: translateX(-50%);
      transition: transform 0.2s ease-in-out;
      background-color: var(--black);
    }

    &--opened {
      background-color: transparent;

      &::before {
        width: 1px;
        height: 35px;
        top: 6px;
        background-color: var(--f-border);
        transform: rotate(45deg);
      }

      &::after {
        width: 1px;
        height: 35px;
        top: 6px;
        background-color: var(--f-border);
        transform: rotate(-45deg);
      }
    }

    &-decoration {
      display: block;
      width: 23px;
      height: 1px;
      position: absolute;
      top: 24px;
      left: 50%;
      transform: translateX(-50%);
      background-color: var(--black);
      transition: opacity 0.1s ease-in-out;
    }

    &--opened > &-decoration {
      opacity: 0;
    }

    @include media($desktop) {
      display: none;
    }
  }
}